import {carIcon} from "../../../api/user.js"
export default{
	props:['carMsg','inner'], 
	data() {
		return {
			carIcon:carIcon 
		}
	},
	methods: {
		changeCar() {
			this.$emit('changeCar');
		},
		opeAddcar(){
			if(uni.getStorageSync('isMobile') != 1) {
				uni.navigateTo({
					url:'/pages/login/login'
				})
				return
			}
			uni.navigateTo({
				url:'/pages/user/add-car/add-car'
			})
		}
	}
}
